import React, { useState, useEffect } from 'react'
import { Link } from 'react-router-dom'
import { useDispatch, useSelector } from 'react-redux';
import {
  CButton,
  CCard,
  CCardBody,
  CCardGroup,
  CCol,
  CContainer,
  CForm,
  CInput,
  CInputGroup,
  CInputGroupPrepend,
  CInputGroupText,
  CRow
} from '@coreui/react'
import CIcon from '@coreui/icons-react'
import { ActionAuth } from '../../../redux/actions/auth';

const Login = (props) => {
  const dispatch = useDispatch();
  const [values, setValues] = useState({ email: '', password: '' });
  const isAuthenticated = useSelector(state => state.auth.isAuthenticated);

  const validateEmail = (email) => {
    var re = /^([\w-]+(?:\.[\w-]+)*)@((?:[\w-]+\.)*\w[\w-]{0,66})\.([a-z]{2,6}(?:\.[a-z]{2})?)$/i;
    return re.test(email);
  }

  const handleChange = (e) => {
    const { name, value } = e.target
    setValues({ ...values, [name]: value })
  }

  const handleSubmit = (e) => {
    e.preventDefault();

    const { email, password } = values;

    if (!(email && password)) {
      alert("Please input email & password")
      return;
    }

    if (!validateEmail(email)) {
      alert("Please input valid email address")
      return;
    }

    const payload = { email, password }
    dispatch(ActionAuth.signIn(payload));
  }

  const keyDownEnter = (e) => {
    if (e.keyCode === 13)
      handleSubmit(e);
  }

  // useEffect(() => {
  //   dispatch(ActionAuth.logoutRequest());
  // }, []);

  useEffect(() => {
    if (isAuthenticated) {
      const { from } = props.location.state || { from: { pathname: "/" } };
      props.history.push(from);
    }
  }, [isAuthenticated]);

  return (
    <div className="c-app c-default-layout flex-row align-items-center">
      <CContainer>
        <CRow className="justify-content-center">
          <CCol md="8">
            <CCardGroup>
              <CCard className="p-4">
                <CCardBody>
                  <CForm>
                    <h1>Login</h1>
                    <p className="text-muted">Sign In to your account</p>
                    <CInputGroup className="mb-3">
                      <CInputGroupPrepend>
                        <CInputGroupText>
                          <CIcon name="cil-user" />
                        </CInputGroupText>
                      </CInputGroupPrepend>
                      <CInput type="text" name="email" placeholder="Email" autoComplete="email" onChange={handleChange} />
                    </CInputGroup>
                    <CInputGroup className="mb-4">
                      <CInputGroupPrepend>
                        <CInputGroupText>
                          <CIcon name="cil-lock-locked" />
                        </CInputGroupText>
                      </CInputGroupPrepend>
                      <CInput type="password" name="password" placeholder="Password" autoComplete="current-password" onChange={handleChange} onKeyDown={keyDownEnter} />
                    </CInputGroup>
                    <CRow>
                      <CCol xs="6">
                        <CButton color="dark" className="px-4" onClick={handleSubmit}>Login</CButton>
                      </CCol>
                      <CCol xs="6" className="text-right">
                        <CButton color="link" className="px-4">Forgot password? (개발중)</CButton>
                      </CCol>
                    </CRow>
                  </CForm>
                </CCardBody>
              </CCard>
              <CCard className="text-white bg-dark py-5 d-md-down-none" style={{ width: '44%' }}>
                <CCardBody className="text-center">
                  <div>
                    <h2>Sign up</h2>
                    <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut
                      labore et dolore magna aliqua.</p>
                    <Link to="/register">
                      <CButton color="dark" className="mt-3" active tabIndex={-1}>Register Now! (개발중)</CButton>
                    </Link>
                  </div>
                </CCardBody>
              </CCard>
            </CCardGroup>
          </CCol>
        </CRow>
      </CContainer>
    </div>
  )
}

export default Login
